.loader {
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  transform-origin: 170px 50px;
  background: #222;
  border: 4px solid #333;
  border-radius: 50%;
  margin: auto;
  animation: panmov .4s ease-in-out infinite alternate;
  display: block;
  position: relative;
  box-shadow: inset 3px 4px rgba(0, 0, 0, .2), inset 0 0 6px rgba(0, 0, 0, .133);
}

.loader:before {
  content: "";
  width: 55px;
  height: 53px;
  background-color: #fff;
  background-image: radial-gradient(3px, rgba(255, 255, 255, .4) 90%, rgba(0, 0, 0, 0) 10%), radial-gradient(12px, #ffc400 90%, rgba(0, 0, 0, 0) 10%), radial-gradient(12px, #ffae00 100%, rgba(0, 0, 0, 0) 0);
  background-position: -4px -6px, -2px -2px, -1px -1px;
  background-repeat: no-repeat;
  background-size: auto;
  background-attachment: scroll;
  background-origin: padding-box;
  background-clip: border-box;
  border-radius: 47% 36% 50% 50% / 49% 45% 42% 44%;
  animation: ylmov .6s ease-in-out infinite alternate;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)skew(-15deg, 15deg)rotate(-15deg);
  box-shadow: inset -2px -3px rgba(0, 0, 0, .133), inset 0 0 4px rgba(0, 0, 0, .2);
}

.loader:after {
  content: "";
  height: 15px;
  width: 70px;
  background: #222;
  border-radius: 0 8px 8px 0;
  position: absolute;
  top: 48px;
  left: 100%;
  transform: rotate(5deg)translateX(3px);
  box-shadow: inset 3px 0 3px rgba(238, 238, 238, .133);
}

@keyframes panmov {
  0%, 10% {
    transform: rotate(5deg);
  }

  90%, 100% {
    transform: rotate(-5deg);
  }
}

@keyframes ylmov {
  to {
    background-position: -2px -4px, 2px 2px, 1px 1px;
    border-radius: 50% 36% 50% 50% / 49% 50% 45% 45%;
  }
}

.loader {
  display: none;
}

.loader.visible {
  display: block;
}

.cat-info img {
  max-width: 50%;
  max-height: 50%;
  padding-top: 20px;
}

